<template>
	<div id="about">
		<div class="about">
			<img class="icon" :src="icon"/>
			<p>App V1.1</p>
			<ul>
				<li class="cont_frame about_list" @click="showModel">
					<span></span>
					<span class="new fr">
						<i class="new_hint"></i>
						<span>V1.2</span>
					</span>
				</li>
			</ul>
		</div>
		<!--  -->
		<PopModel :show="isShow" @close="hideModel">
			<div slot="content">
				<div class="title"> V1.2 </div>
				<p>1.</p>
				<p>2.</p>
				<p>3.</p>
			</div>
			<div slot="footer">				
				<button class="button" style="width: 100%;" @click="download"></button>
			</div>
		</PopModel>
	</div>
</template>

<script>
	// 
	import PopModel from "components/Modal/PopModal.vue";
	// js
	import Common from 'common/common.js';
	// Api
	import Api from "api/about.js";

	export default {
		name: "about",
		components: { PopModel },
		data(){
			return{
				// 
				icon: require('@/assets/images/icon_256x256.png'),
				// 
				isShow: false
			}
		},
		created(){
			this.init();
		},
		methods:{
			init(){
                this.$store.commit('SET_NAV_TITLE', '');
			},
			showModel(){
				this.isShow = true;
			},
			hideModel(){
				this.isShow = false;
			},
			// 
			download(){
				this.hideModel();
			}
		}
	};
</script>

<style lang="less">
	// 
	@import "../../assets/less/setting";

	/* about_cont */

	.about{

		text-align: center;

		.icon{
			.wd(80);
			.ht(80);
			.mt(30);
			.mb(20);
		}

		ul{
			text-align: left;
			.mt(30);

			li{
				cursor: pointer;
			}
		}
		
		.new_hint{
			background: @warn_color;
			color:#fff;
			padding: 2px 5px;
			font-style: normal;

			.ft(12);
			.mr(5);
			.border_radius(4);
		}
	}

	.title{
		color: @base_color;
		font-weight: bold;

		.ft(16);
		.mb(15);
	}
</style>



